/* @use "../../../node_modules/aos/src/sass/aos.scss"; */
/*transitions*/
/*


navigation underline effect

*/
.nav:not(.nav--social) .nav__item a {
  transition: text-shadow 0.3s ease;
  position: relative;
}
.nav:not(.nav--social) .nav__item a:after {
  content: "";
  width: 0%;
  position: absolute;
  height: 2px;
  background-color: var(--clr-primary);
  bottom: -10px;
  left: 50%;
  translate: -50%;
}
.nav:not(.nav--social) .nav__item a:hover::after {
  width: 100%;
  animation: scale 0.3s ease;
}

.nav--social .nav__item a svg path {
  transition: fill 0.3s ease;
}
.nav--social .nav__item a svg {
  transition: scale 0.3s ease;
}
.nav--social .nav__item a:hover svg {
  scale: 1.1;
}

.logo.text-gradient {
  transition: background-position 0.3s ease;
}
.logo.text-gradient:hover {
  background-position: 0px;
}

.card {
  transition: all 0.3s ease !important;
}
.card:hover {
  scale: 1.02;
}

@keyframes scale {
  from {
    width: 0%;
  }
  to {
    width: 100%;
  }
}
@media (prefers-reduced-motion: reduce) {
  .my-element {
    transition: none; /* Letiltja az átmeneteket a csökkentett mozgáshoz */
  }
}
/* mobile nav animation under medium size*/
@media (max-width: 768px) {
  nav {
    /*      visibility: visible;
    translate: 0;
    .active {
        opacity: 1;
    } */
  }
  nav .wrapper.active {
    animation: fadeIn 0.3s ease;
  }
}
@keyframes fadeIn {
  from {
    opacity: 0;
    translate: -100vw;
  }
  to {
    opacity: 1;
    translate: 0;
  }
}
