@media (max-width: 768px) {
  nav .wrapper {
    flex-direction: column;
    width: 100vw;
    position: absolute;
    align-items: flex-start;
    top: 0;
    left: -1.5rem;
    padding: 2rem;
    background-color: var(--clr-white);
    box-shadow: 0 -4px 40px 0 rgba(0, 0, 0, 0.355);
    z-index: 10;
    visibility: hidden;
  }
  nav .wrapper.active {
    visibility: visible;
  }
