  }
  nav .flex .nav:not(.nav--social) {
    flex-direction: column;
    align-items: flex-start;
  }
  nav .nav-toggle {
    visibility: visible;
    aspect-ratio: 1;
    width: 51px;
    position: absolute;
    top: 1rem;
    right: 0;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    gap: 0.2rem;
    padding: 0.5rem;
    z-index: 20;
  }
  nav .nav-toggle__line {
    width: 100%;
    height: 4px;
    background-color: var(--clr-primary);
    align-self: flex-end;
    border-radius: 5px;
  }
  nav .nav-toggle:hover {
    outline: 1px solid var(--clr-primary-10);
    border-radius: 5px;
  }
